nav {
    background: rgb(255, 255, 255);
    background: linear-gradient(135deg, rgba(255, 255, 255, 1) 30%, rgba(218, 218, 218, 1) 50%);
    border-bottom: 2px solid rgb(0, 0, 0);

}

/* Links */
/* redes sociais */
nav i {
    font-size: 40px;
}
/*  redes sociais dropmenu config */
.dropdown-item span {
    position: relative;
    top: -10px;
    left: +3px;
}

/* estilo dos links da navbar */
nav ul a:hover {
    border-bottom: 2px solid black;
    color: black;
    border-radius: .0rem;
}
/* retira as bordas dos links na versao mobile */
@media only screen and (max-width: 992px) {
    nav ul a:hover {
        border: none;
    }
}

/* coloca margin top nos links quando os links estão retraídos */
@media only screen and (max-width: 991px) {
    .navbar-nav {
        margin-top: 10px;
    }
}

/* configuração do dropdown menu e links*/
nav .dropdown-menu {
    background: rgba(218, 218, 218, 1);
    border: 1px solid rgb(0, 0, 0);
}
/* dropmenu item style */
nav .dropdown-item {
    color: rgb(0, 0, 0) !important;
    border: 2px solid transparent;
    background: transparent !important;
    border-radius: 0 !important;
}
