@import './Navbar.css';
@import "./Carousel.css";
@import "./Principal.css";
@import './Footer.css';

/* Configurações gerais */
body {
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
    color: white;
    background-color: black;
}
/* link config */
a {
    border-bottom: 2px solid transparent;
    color: black;
}

a:hover {
    color: rgb(95, 95, 95);
    text-decoration: none;
}


/* --------------------------------------- */
/* classes */
/* --------------------------------------- */

/* margin & padding */
.mt-150 {
    margin-top: 150px;
}
.lemes-bg-padding {
    padding: 75px 0px;
}


/* Buttons */
.btn-gradient {
    padding: 7px 25px;
    color: white !important;
    background-image: linear-gradient(to right bottom, #0ecdf0, #00bbfc, #00a6ff, #008dff, #006fff, #005dfd, #1e49f8, #362ef1, #452be7, #5028dd, #5725d3, #5d23c9);
}
.btn-gradient:hover {
    background-image: linear-gradient(to right bottom, rgba(14, 205, 240, 1) 0%, rgba(93, 35, 201, 1) 100%);
}


/* Background */
.lemes-bg {
    background: rgb(255, 255, 255);
    background: linear-gradient(135deg, rgba(255, 255, 255, 1) 30%, rgba(218, 218, 218, 1) 50%);
    color: black;
    border-bottom: 1px solid white;
}
.lemes-bg-alt {
    background: rgb(255, 255, 255);
    background: linear-gradient(135deg, rgba(255, 255, 255, 1) 30%, rgba(218, 218, 218, 1) 50%);
    color: black;
    border-bottom: 1px solid white;

}

